.glassmorphism-card {
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(16.5px);
}

.welcome-page__container {
  position: fixed;
  inset: 0;
  z-index: 100;
  height: 100%;
  width: 100vw;
  transition: 0.4s;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 3rem 1rem;
  transition: all 0.3s ease;
}
.welcome-page__image-container {
  height: 40vh;
  width: 90%;
}
.welcome-page__title {
  text-align: center;
  color: var(--black);
  padding: 2rem 0;
}
.welcome-page__subtitle {
  text-align: center;
  color: var(--black);
}
.welcome-page__second-subtitle {
  text-align: center;
  opacity: 0.4;
}
.welcome-page__btn {
  padding: 1.3em 3em;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  background-color: var(--blue);
  color: var(--white);
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.items-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 90;
  max-width: 100%;
  min-height: 100vh;
  overflow-y: auto;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  transition: transform 0.5s cubic-bezier(0.25, 0.45, 0.45, 0.95);
  background: url(/assets/Glassmorphism-Background.png);
  background-size: cover;
}
.items-container__greetings, .items-container__title {
  display: flex;
}
.items-container__title input {
  margin-left: 0.5rem;
  flex: 1 1 0%;
  min-width: 0;
  appearance: none;
  border: none;
  outline: none;
  background: none;
}
.items-container__title, .items-container__title input,
.items-container .your-schedule {
  color: var(--black);
  font-size: 1.5rem;
  font-weight: 500;
}
.items-container__current-date {
  display: flex;
  justify-content: space-between;
  border-radius: 30px;
  padding: 0 0.5rem;
}
.items-container__current-date .text-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 1rem;
}
.items-container__current-date .text-wrapper p {
  font-size: 1rem;
}
.items-container__current-date .image-wrapper {
  height: 15vh;
  width: 100%;
}
.items-container__category-container {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: auto;
}
.items-container__category-container .title,
.items-container__category-container .subtitle,
.items-container__category-container .category-counter {
  text-align: center;
  font-size: 1.2rem;
}
.items-container__category-container .subtitle {
  font-size: 0.9rem;
  padding-bottom: 3rem;
}
.items-container__category-container .title,
.items-container__category-container .subtitle {
  opacity: 0.7;
}
.items-container__category-container .round-plus {
  position: relative;
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--blue);
  color: var(--white);
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.3);
  align-self: center;
  margin-top: 2rem;
}
.items-container__category-container .round-plus i {
  color: var(--beige);
  line-height: 60px;
  font-size: 1.5rem;
}

/* Modal */
.modal-radio {
  position: absolute;
  visibility: hidden;
  opacity: 0;
}

.modal {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  right: auto;
  bottom: auto;
  overflow: hidden;
  opacity: 0;
  transform: scale(0);
  transition: opacity 0.25s, transform 0.25s;
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 3rem;
}
.modal .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
}
.modal .content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  max-height: 90vh;
  background-color: rgba(153, 237, 209, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  box-shadow: 0 3px 15px -2px #222;
  padding: 4rem 2rem;
}
.modal .content .top {
  margin-bottom: 2rem;
}
.modal .fa-circle-xmark {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 2.5rem;
}
.modal form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.modal form .input-field {
  position: relative;
  width: 100%;
}
.modal form .input-field label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: var(--black);
  transition: 0.2s all;
  cursor: text;
}
.modal form .input-field input {
  width: 100%;
  border: 0;
  outline: 0;
  padding: 0.5rem 0;
  background-color: transparent;
  border-bottom: 2px solid var(--black);
  box-shadow: none;
  color: #111;
}
.modal form .input-field input:invalid {
  outline: 0;
}
.modal form .input-field input:focus,
.modal form .input-field input:valid {
  border-color: var(--blue);
}
.modal form .input-field input:focus ~ label,
.modal form .input-field input:valid ~ label {
  font-size: 14px;
  top: -24px;
  color: var(--blue);
}
.modal form input[type=submit] {
  margin-top: 1rem;
  padding: 1.3em 3em;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: var(--black);
  background-color: transparent;
  border: 1px solid var(--black);
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  outline: none;
}
.modal form input[type=submit]:hover {
  background-color: var(--black);
  color: var(--white);
}

#modal-open:checked + .modal {
  opacity: 1;
  transform: scale(1);
}

.category-box-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin-top: 2rem;
}

.category-wrapper {
  display: flex;
  min-height: 25vh;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  transition: all 0.5s ease;
}

.category-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
  border-radius: 15px;
  min-height: 15vh;
  transition: all 0.5s ease;
  cursor: pointer;
}
.category-box__task {
  padding-bottom: 1rem;
}
.category-box__name {
  border: none;
  outline: none;
  font-size: 1.3rem;
  background-color: transparent;
  text-align: center;
}

.btn-container {
  display: flex;
  justify-content: space-between;
  padding-right: 1rem;
  margin: 1rem 0;
}

.button-open {
  margin-top: 0.3rem;
  padding: 1em 3em;
  font-size: 9px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: var(--black);
  background-color: transparent;
  border: 1px solid var(--black);
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  z-index: 1;
  width: 100%;
}

.edit-btn,
.delete-btn {
  cursor: pointer;
  margin: 0 0.5rem;
  padding: 0 1.5rem;
  color: var(--black);
  font-size: 1rem;
  border-radius: 45px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(2px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-right-color: rgba(255, 255, 255, 0.1);
  border-bottom-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fall {
  transform: translateY(8rem) rotateZ(20deg);
  opacity: 0;
}

.tasks-counter {
  margin: 0;
  font-size: 0.8rem;
  width: 100%;
  text-align: center;
}

.fullscreen {
  position: absolute;
  z-index: 9999;
  width: 96%;
  min-height: 100vh;
  height: auto;
  top: 4rem;
  bottom: 0;
  padding: 3rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(16.5px);
  border-radius: 45px;
  will-change: auto;
}

/* Items/Task To Do Container/Input Form - Create Single Item */
.task-elements-container {
  display: none;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 1rem;
}

.task-input {
  margin: 0;
  padding: 0.2rem 1rem;
  font-size: 1rem;
  line-height: 1.8rem;
  border: none;
  outline: none;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  font-size: 15px;
  padding-left: 30px;
  transition: all 0.3s ease;
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(15px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
  border-radius: 50px;
}

.task-input:focus {
  background-color: rgba(0, 0, 0, 0.21);
}

.task-input::placeholder {
  color: var(--black);
}

.task-input:focus {
  outline: none;
}

.new-task-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.create-task-btn {
  width: 50%;
  align-self: center;
  padding: 1em 3em;
  font-size: 12px;
  background-color: var(--blue);
  color: var(--white);
  margin: 1rem 0;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
}